.product-card{
  display:flex;
  flex-direction: column;
  @media (max-width: @mobile-width-only) {
    margin-top: 30px;
   }
  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    &:nth-child(n+2){
      margin-top: 54px;
    }
   display:grid;
   grid-template-columns: 50% 50%;
   height: 334px;
  }
  @media (min-width: @desktop-width) {
    width: 30%;
   }
}
.product-card__header-link {
  text-decoration: none;
}
.product-card__img{
  width:100%;
  @media (min-width: @tablet-width) {
    grid-column: 1/2;
    height: 100%;
   }
   @media (min-width: @desktop-width) {
    height: 462px;
   }
}
.product-card__content-container{
  border: 1px solid @gallery;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 1;
  @media (max-width: @mobile-width-only) {
    padding: 28px 25px 26px 27px;
   }
  @media (min-width: @tablet-width) {
    grid-column: 2/3;
    padding: 27px 31px 24px 32px;
   }
   @media (min-width: @desktop-width) {
    padding: 27px 32px 25px 26px;
   }

}
.product-card__header {
  @media (max-width: @mobile-width-only){
  }
  @media (min-width: @tablet-width){
    font-size: 20px;
    line-height: 24px;
  }
  @media (min-width: @desktop-width){
  }
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: @black;
  margin: 0;

}

.product-card__description {
  margin-bottom: 0;
  font-weight: 400;
  font-size: 13px;
  line-height: 30px;
  color: @tundora;
  @media (max-width: @mobile-width-only){
    margin-top: 5px;
  }
  @media (min-width: @tablet-width){
    font-size: 15px;
    line-height: 30px;
    margin-top: 6px;
  }
  @media (min-width: @desktop-width){
    margin-top: 6px;
  }
}

.product-card__price {
  font-weight: 700;
  font-size: 15px;
  line-height: 24px;
  margin: 0;
  @media (max-width: @mobile-width-only){
  }
  @media (min-width: @tablet-width){
    font-size: 17px;
    line-height: 24px;
  }
  @media (min-width: @desktop-width){
  }

}

.product-card__price-icon-wrapper {
  @media (max-width: @mobile-width-only){
    margin-top: 16.5px;
    padding-top: 22.5px;
    border-top: 1px solid @gallery;
  }
  @media (min-width: @tablet-width){
  }
  @media (min-width: @desktop-width){
    margin-top: 36px;
  }
  display: flex;
  justify-content: space-between;
}
